var _this;
import wInput from '../../components/watch-login/watch-input.vue' //input
import wButton from '../../components/watch-login/watch-button.vue' //button
import {
    mapState,
    mapMutations
} from 'vuex'

export default {
    data () {
        return {
            logoImage: '../../static/logo-login.png',
            phoneData: '', // 用户/电话
            passData: '', //密码
            verCode: "", //验证码
            showAgree: true, //协议是否选择
            isRotate: false, //是否加载旋转
        }
    },
    components: {
        wInput,
        wButton,
    },
    mounted () {
        _this = this;
    },
    methods: {
        ...mapMutations(['login', 'changeUserName']),
        go (url) {
            uni.navigateTo({
                url: url
            })
        },
        isShowAgree () {
            //是否选择协议
            _this.showAgree = !_this.showAgree;
        },
        getVerCode () {
            //获取验证码
            if (_this.phoneData.length != 11) {
                uni.showToast({
                    icon: 'none',
                    position: 'bottom',
                    title: '请输入11位数正确的手机号码'
                });
                return false;
            }
            // if (_this.passData.length < 6) {
            //     uni.showToast({
            //         icon: 'none',
            //         position: 'bottom',
            //         title: '请输入不少于6位数的新密码'
            //     });
            //     return false;
            // }
            console.log("获取验证码")
            this.$refs.runCode.$emit('runCode'); //触发倒计时（一般用于请求成功验证码后调用）
            uni.showToast({
                icon: 'none',
                position: 'bottom',
                title: '发送成功'
            });

            setTimeout(function () {
                _this.$refs.runCode.$emit('runCode', 0); //假装模拟下需要 终止倒计时
                // uni.showToast({
                //     icon: 'none',
                // 	position: 'bottom',
                //     title: '模拟倒计时终止'
                // });
            }, 30000)
        },
        startReg () {
            //注册
            if (this.isRotate) {
                //判断是否加载中，避免重复点击请求
                return false;
            }
            // if (this.showAgree == false) {
            //     uni.showToast({
            //         icon: 'none',
            //         position: 'bottom',
            //         title: '请先同意《协议》'
            //     });
            //     return false;
            // }
            if (this.phoneData.length != 11) {
                uni.showToast({
                    icon: 'none',
                    position: 'bottom',
                    title: '请输入11位数正确的手机号码'
                });
                return false;
            }
            // if (this.passData.length < 6) {
            //     uni.showToast({
            //         icon: 'none',
            //         position: 'bottom',
            //         title: '请输入不少于6位数的新密码'
            //     });
            //     return false;
            // }
            // if (this.verCode.length != 4) {
            //     uni.showToast({
            //         icon: 'none',
            //         position: 'bottom',
            //         title: '验证码不正确'
            //     });
            //     return false;
            // }
            console.log("验证成功")

            // =====
            _this.isRotate = true
            this.login();
            this.changeUserName(this.phoneData);
            uni.setStorageSync('info', {
                userName: this.phoneData,
                password: this.passData
            });
            setTimeout(function () {
                uni.showToast({
                    icon: 'success',
                    position: 'bottom',
                    title: '验证成功'
                });
            }, 2000)
            setTimeout(function () {
                _this.isRotate = false
                uni.switchTab({
                    url: '/pages/home/index'
                });
            }, 3000)
            // =======
        }
    }
}
